import interfaces from '../../utils/interfaces.js';
var common = require('../../utils/common.js');
import app from "../../App.vue";
//弹窗
import pop from "../../components/ming-pop.vue";
export default {
	components:{
		pop
	},
	data() {
		return {
			master_info: '',
			data:[],
			id: 0,
			is_preview:false,
			//弹窗位置，默认上方
			direction: "up",
		}
	},
	onLoad(options) {
		this.id = options.id ? options.id : 0;
	},
	onShow() {
		this.master_info = app.globalData.master_info;
		if (this.master_info) {
			if(!this.is_preview){
				this.initData();
			}
		} else {
			wx.reLaunch({
				url: '/pages/auth/index',
			})
		}
	},
	methods: {
		//初始化订单详情
		initData(){
			this.request({
				url: interfaces.orderHallDetail,
				data: {
					id: this.id,
				},
				header: {
					token: this.master_info.token
				},
				method: 'POST',
				success: res => {
					//console.log(res);
					if (res.code == 200) {
						
						this.is_preview=true;
											
						if(res.data.pics!=''){
							res.data.pics = res.data.pics.split(",");
						}else{
							res.data.pics=[];
						}
						
						res.data.create_time_txt = common.formatDT(res.data.create_time, 'yyyy.MM.dd h:m');
						this.data=res.data;
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			})
		},
		//点击报修图片预览图片事件
		preImage(url) {
			wx.previewImage({
				current: url,
				urls: this.data.pics,
			})
		},
		//师傅确认完工
		finish(id){
			var _this = this;
			wx.showModal({
				title: '提示',
				content: '确认完工了吗？',
				success: function(res) {
					if (res.confirm) {
						_this.request({
							url: interfaces.orderPtFinish,
							header: {
								token: _this.master_info.token
							},
							data: {
								id: id
							},
							method: 'POST',
							success: res => {
								console.log(res);
								if (res.code == 200) {
									_this.initData();
								} else {
									app.isLogin(res.code, res.msg);
								}
							}
						})
					}
				}
			})
		},
		//弹出报价框
		start(){
			if(this.data.type==2){
				//如果是报价下单，则去报价
				this.open('center');
			}else{
				//如果是一口价订单，则直接抢单
				this.baoMing();
			}
		},
		//一口价订单，师傅直接抢单
		baoMing(){
			this.request({
				url: interfaces.orderBaoMing,
				data: {
					order_no:this.data.order_no
				},
				header: {
					token:this.master_info.token
				},
				method: 'POST',
				success: res => {
					if (res.code == 200) {
						wx.showToast({
							title: res.msg,
							icon: 'success'
						})
						this.data.baoming=1;
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			});
		},
		//表单提交事件，报价订单，师傅报价抢单
		formSubmit(e) {
			var _this = this;
			var data = e.detail.value;
			data.order_no=this.data.order_no;
			if (!data.price >0) {
				wx.showToast({
					title: '请输入正确的价格',
					icon: "none",
				});
				return;
			}
			//console.log(data);
			this.request({
				url: interfaces.orderBaoMing,
				data: data,
				header: {
					token: _this.master_info.token
				},
				method: 'POST',
				success: res => {
					if (res.code == 200) {
						wx.showToast({
							title: res.msg,
							icon: 'success'
						})
						this.$refs.pop.close();
						this.data.baoming=1;
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			});
		},
		//弹窗组件
		open(direction) {
			this.direction = direction;
			//打开弹窗
			this.$refs.pop.show();
		},
		//提示
		tip(){
			wx.showToast({
				title: '您的申请已经提交了',
				icon: "none",
			});
		}
	}
}
